<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-top">
        <div class="ct-left">
          <div class="ct-l-l">
            <ctitle :title="'重件占比'"></ctitle>
              <allCount class="all-count" :count="allcount"></allCount>
              <ring :params="zbRingParams" class="zb-ring"></ring>
          </div>
          <div class="ct-l-r">
            <ctitle :title="'区划分布'"></ctitle>
            <div class="charts">
              <div
                v-if="barBackStatus"
                class="map-back"
                @click="barBack"
              ></div>
              <barlineHight :params="qvfbBarParams" @canvasClick='canvasClick'></barlineHight>
            </div>
          </div>
        </div>
        <div class="ct-right">
          <div class="echartPie">
            <ctitle :title="'四级登记机构分布'"></ctitle>
            <div class="charts">
              <loopPie :params="dqfbPieParams"></loopPie>
            </div>
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <div class="w-charts">
          <ctitle :title="'信访人群年龄分布'"></ctitle>
          <div class="charts">
            <barline :params="rqnlBarParams"></barline>
          </div>
        </div>
        <div class="w-charts w-center">
          <ctitle :title="'信访目的分布'"></ctitle>
          <div class="charts chart-pie">
            <pie-chart3D :params="xfmdPieParams"></pie-chart3D>
          </div>
        </div>
        <div class="w-charts">
          <ctitle :title="'投诉问题分布'"></ctitle>
          <div class="charts chart-loop">
            <loopPie :params="tswtPieParams"></loopPie>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import incNumber from "@/components/incNumber/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import loopPie from "@/components/pie/LoopPie";
  import barline from "@/components/bar/BarLine";
  import barlineHight from "@/components/bar/BarLine1";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";
  import ring from "@/components/pie/ring";

  export default {
    name: "cjcj",
    components: {
      ctitle,
      incNumber,
      PieChart3D,
      loopPie,
      barline,
      allCount,
      detailList,
      ring,
      barlineHight
    },
    data() {
      return {
        mapJson: {
          广东省: "440000",
          广州市: "440100",
          韶关市: "440200",
          深圳市: "440300",
          珠海市: "440400",
          汕头市: "440500",
          佛山市: "440600",
          江门市: "440700",
          湛江市: "440800",
          茂名市: "440900",
          肇庆市: "441200",
          惠州市: "441300",
          梅州市: "441400",
          汕尾市: "441500",
          河源市: "441600",
          阳江市: "441700",
          清远市: "441800",
          东莞市: "441900",
          中山市: "442000",
          潮州市: "445100",
          揭阳市: "445200",
          云浮市: "445300"
        },
        barBackStatus: false,
        curCity: '广东省',
        shijCity: "",
        shej_02: "invalid",
        shij_02: "invalid",
        xj_02: "invalid",
        // 信访量初信重信占比
        allcount: -1,
        detailList: null,
        // 四级登记机构分布
        dqfbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.pieMoreColors,
            // radius: ["30%", "65%"],
            radius: ["43%", "60%"],
            center: ["40%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            tooltip: {
              show: true
            },
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 5,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "32%", // label字号
            rollFlag: true,
          }
        },
        // 重件占比(圆环)
        zbRingParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 165,
            radius: ['54%', '65%'],
            bgRadius: ['59%', '60%'],
            center: ["50%", "50%"],
            colors: [
              ["#fee89f", "#eea570"]
            ],
          }
        },
        // 信访次数占比
        xfcsBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: false,
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "5%",
            top: "5%",
            bottom: "10%",
            barWidth: 30,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#ffe97d", "#ff957c"]]
            },
            map: {
              value: "次数"
            }
          }
        },
        // 区划分布
        qvfbBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "85%"
          },
          componentType: 'series',
          options: {
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: "12%", // label字号
            left: "5%",
            right: "5%",
            top: "0%",
            bottom: "0%",
            barLabelOptions: "top",
            barWidth: "35%", // 每个柱形图宽
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "数量"
            }
          }
        },
        // 信访人群年龄分布
        rqnlBarParams: {
          data: [],
          canvasStyle: {
            width: "90%",
            height: "95%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: true,
            axis1Name:"",
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "10%",
            top: "10%",
            bottom: "10%",
            barLabelOptions: "top",
            // barWidth: 30,
            barWidth: "35%", // 每个柱形图宽
            splitLineShow: false,
            barLabelShow: true, //柱状图数量值显示
            // axisLabelColor: "rgba(0, 176, 255, .8)",
            // itemLabelColor: "rgba(0, 176, 255, .8)",
            // barGap: '30%',// 柱间距离，
            colors: {
              bar: [["#D5FA9E", "#9ADE72"]]
            },
            map: {
              value: "人数"
            },
          }
        },
        // 信访目的
        xfmdPieParams: {
          data: [],
          angle: 20,
          radius: "35%",
          labelRadius: 25,
          fontSize: this.fontSize(0.16),
          color:this.$STANDARD.pieColors,
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 投诉问题
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.15),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.2)
            },
            length: 20,
            length2: 50,
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 0,
            borderColor: "#0B2244",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "10%" //文字大小
            },
            labelSize: "22%", // label字号
            threeLevel:true,
            rollFlag: true
          }
        },
        // 地区信访次数
        dqxfcsBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: false,
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "5%",
            top: "5%",
            bottom: "10%",
            barWidth: 30,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#d2f69d", "#79cc7a"]]
            },
            map: {
              value: "次数"
            }
          }
        },
      };
    },
    mounted() {
      this.getAPIAndMap(this.date);
    },
    methods: {
      canvasClick(item) {
        this.barBackStatus = true;
        this.curCity = item.params.name;
        let obj={color:'#fefa82'};
        let c = ''
        for(let key in this.mapJson) {
          if (key === item.params.name) {
            c = key;
          }
        }
        if (!c) {
          this.qvfbBarParams.data.forEach((item,index)=>{
            if(this.curCity == item.name){
            // this.countyBackShow = true
              this.$set(item, "itemStyle", obj)
            }else{
              this.$delete(item,"itemStyle");
            }
          })
        }
      },
      getQyfbAPI(date) {
        if (this.curCity === "广东省") {
          this.shej_02 = this.curCity;
          this.shij_02 = "invalid";
          this.xj_02 = "invalid";
          this.nameP = "shij_02";
        } else {
          // 市级
          if (this.shijCity) {
            this.shej_02 = "广东省";
            this.shij_02 = this.shijCity;
            this.nameP = "xj_02";
            if (this.xj_02 !== 'invalid') {
              this.shej_02 = "广东省";
              this.shij_02 = this.shijCity;
              this.xj_02 = this.curCity;
            } else {
              this.shej_02 = "广东省";
              this.shij_02 = this.shijCity;
              this.xj_02 = 'invalid';
            }
          } else {
            this.shej_02 = "广东省";
            this.shijCity = 'invalid',
            this.xj_02 = 'invalid'
            this.nameP = "invalid";
          }
        }
        // 3.区划分布(柱状图)
        get_http("qjywjkCfxfjkQyfbBar", {
          day: `[${date.startDate},${date.endDate}]`,
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
        }).then(res => {
          console.log('市级',res.data)
          this.qvfbBarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      },
      getAPIAndMap(date) {
        this.getQyfbAPI(date)
        this.getAPI(date)
      },
      getAPI(date) {
        // 1.重件占比(总数)+2.重件占比（圆环图）
        get_http("qjywjkCfxfjkZbTotal", {
          day: `[${date.startDate},${date.endDate}]`,
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
        })
        .then(res => {
          let arr = res.data.data;
          let total = this.sum(arr); // 总数
          let cjData = {}
          for (let i=0;i<arr.length;i++) {
            if (arr[i].name === '重件') {
              this.allcount = arr[i].value
              cjData = arr[i]
              cjData.percent = (this.allcount/total).toFixed(2)
            }
          }
          this.zbRingParams.data = [cjData];
        })
        .catch(err => {
          console.log(err);
        });
          
        // 4.四级登记机构分布
        get_http("qjywjkCfxfjkSjjgfbRing", {
          day: `[${date.startDate},${date.endDate}]`,
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
        }).then(res => {
          this.dqfbPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        // 5.信访人群年龄分布(柱形图)
        get_http("qjywjkCfxfjkRqnlfb", {
          day: `[${date.startDate},${date.endDate}]`,
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
        }).then(res => {
          this.rqnlBarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        // 6.信访目的分布
        get_http("qjywjkCfxfjkMdfb", {
          day: `[${date.startDate},${date.endDate}]`,
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
        }).then(res => {
          this.xfmdPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        // 7.投诉问题分布
        get_http("qjywjkCfxfjkTswtfb", {
          day: `[${date.startDate},${date.endDate}]`,
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
        })
        .then(res => {
          this.tswtPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
      // 求和方法
      sum(arr) {
        let sum = 0
        for(let i=0;i<arr.length;i++) {
          sum += arr[i].value
        }
        return sum
      },
      // 地图返回全省
      barBack() {
        this.barBackStatus = false;
        this.curCity = "广东省";
      },
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPIAndMap(val);
      },
      curCity: function(newVal, oldVal) {
        if (newVal === '广东省') {
          this.getAPIAndMap(this.date)
        } else {
          let c = "";
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.getAPIAndMap(this.date);
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            console.log('this.xj_02 ', this.xj_02 )
            // this.getQyfbAPI(this.date);
            this.getAPI(this.date);
          }
        }
      }
    }
  };
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.8rem
  height calc(100% - 0.8rem)
  .content
    width 100%
    height 100%
    display flex
    flex-direction column
    padding .2rem .3rem
    box-sizing border-box
    .c-top
      flex 1
      display flex
      margin-top 0.07rem
      .ct-left
        flex 2
        height 100%
        position relative
        display flex
        .ct-l-l
          flex 1
          height 100%
          .all-count
            margin-top 0.3rem
          .zb-ring
            height 2.75rem
        .ct-l-r
          flex 1
          height 100%
          position relative
          .charts
            width 100%
            margin-left -0.4rem
            height calc(100% - 0.28rem)
            .map-back
              position absolute
              background url("~@/assets/back-icon.png") no-repeat
              top 30px
              right 60px
              width 30px
              height 30px
              z-index 1000
              font-size 0.2rem
              color #fff
              cursor pointer
      .ct-right
        flex 1
        height 100%
        position relative
        display flex
        .charts
          width 100%
          height calc(100% - 0.28rem)
        .echartPie
          flex 1
          height 100%
        .echartBar
          flex 1
          height 100%
    .c-bottom
      flex 1
      background-size 100% 100%
      position relative
      display flex
      box-sizing border-box
      .w-charts
        flex 1
        height 100%
        .charts
          width 100%
          height 90%
        .chart-pie
          width 85%
        .chart-loop
          width 85%
</style>
